import request from 'request-promise';
import tickerFactory from './model';

export default async function (symbol) {
  const btcUsdRequestOpts = {
    uri: `https://api.bitfinex.com/v2/ticker/${symbol}`,
    json: true,
  };
  const data = await request(btcUsdRequestOpts);

  return tickerFactory.create('bitfinex', symbol, data);
}
